@charset "UTF-8";
/* Reset y Variables */
* {
  margin: 0;
  padding: 0;
}

:root {
  --main-color: rgb(117, 55, 61);
  --secondary-color: white;
  --background-color: #5562AB;
  --font-family-primary: "Inria Sans";
  --font-family-secondary: "Montserrat";
  --border-color: black;
  --border-size: 2px;
  --button-radius: 500px;
  --padding-standard: 1.18rem;
  --presentation-height: 245px;
  --grid2-width: 420px;
  --grid4-width: 260px;
}

/* Estilos Generales */
body {
  background-color: white;
  justify-content: center;
  font-weight: bold;
}

h1 {
  font-family: var(--font-family-primary);
  font-size: large;
  font-weight: bold;
  margin-left: 20px;
  color: var(--main-color);
}

p {
  font-family: var(--font-family-secondary);
}

/* Header */
header {
  color: var(--secondary-color);
  font-size: 1rem;
  position: fixed;
  width: 100%;
  height: 10%;
  z-index: 3;
  background-color: rgba(0, 0, 0, 0.892);
}

header nav {
  align-items: center;
  justify-self: center;
}

header li {
  color: var(--secondary-color);
  display: inline-flex;
  padding: var(--padding-standard);
  cursor: pointer;
}

header li:hover {
  color: var(--main-color);
  transition: color 1s ease;
  text-underline-offset: dashed;
}

/* Navegación de Productos */
.navproducts {
  align-items: center;
  justify-self: center;
  padding: 20px;
}

.navproducts li {
  display: inline-flex;
  padding: var(--padding-standard);
  cursor: pointer;
}

/* Tarjetas de Productos */
.productscard {
  margin: 20px;
  background-color: var(--background-color);
  height: 450px;
  align-items: center;
  border: var(--border-size) solid var(--border-color);
  color: var(--secondary-color);
  text-shadow: -1px -1px 0px black, 1px -1px 0px black, -1px 1px 0px black, 1px 1px 0px black;
}

.productscard figure {
  height: 240px;
  width: 240px;
}

.productscard img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.productscard td {
  padding: 25px;
}

/* Detalles */
.details td {
  padding: 25px;
}

.details p,
.details li,
.app p,
.app li {
  margin-left: 40px;
  padding: 7px;
  align-self: center;
}

/* Estilo para Presentación */
#presentation {
  width: 100%;
  height: var(--presentation-height);
}

#laptop {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  pointer-events: none;
}

.details img {
  pointer-events: none;
}

.cardBlog .top {
  height: 100%;
  width: 200px;
  margin: 0.5rem;
  flex-shrink: 0;
  cursor: pointer;
}

.cardBlog .round {
  height: 200px;
}

.round {
  border-radius: 1.5rem;
  overflow: hidden;
  margin-bottom: 1rem;
  position: relative;
}

img:hover {
  transform: scale(1.05);
}

/* Carrito de Compras */
.cart img {
  margin: 10px;
  width: 150px;
  height: 150px;
  border: var(--border-size) solid var(--border-color);
  border-radius: 30px;
  float: left;
}

.cart table {
  margin: 25px;
}

.cart .total {
  margin-left: 55%;
}

.cart tr {
  text-align: left;
}

.cart td {
  padding-right: 13px;
}

.cart h1,
.cart p {
  margin: 0;
}

.cart h1 {
  font-size: 16px;
}

.cart p {
  padding: 0;
  font-size: 13px;
}

#check {
  height: 70px;
  background-color: #6A14BF;
  font-size: 22px;
  font-weight: bold;
  text-shadow: -1px -1px 0px black, 1px -1px 0px black, -1px 1px 0px black, 1px 1px 0px black;
}

#check:hover {
  background-color: #b392d3;
}

/* Footer */
footer {
  font-family: var(--font-family-secondary);
  font-weight: 800;
  color: rgb(168, 162, 162);
}

/* SweetAlert */
.swal-popup {
  width: 95%;
  padding: 20px;
  border-radius: 20px;
}

.swal-title {
  font-size: 24px;
}

.swal-content {
  font-size: 18px;
  text-align: left;
}

.swal-confirm-button {
  width: auto;
}

button {
  margin: 2.25rem;
  background-color: black;
  color: var(--secondary-color);
  border-radius: var(--button-radius);
  width: 75%;
  margin-bottom: 25px;
}

button:hover {
  background-color: rgb(100, 94, 94);
}

.object img {
  border: var(--border-size) solid rgba(0, 0, 0, 0.362);
}

.object .grid2,
.object .grid4,
.object .grid5 {
  margin: 20px;
  float: left;
}

.object .grid2 figure,
.object .grid4 figure,
.object .grid5 figure {
  width: 100%;
  height: 100%;
}

.object .grid2 img,
.object .grid4 img,
.object .grid5 img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

@media (min-width: 768px) {
  :root {
    --grid2-width: 420px;
    --grid4-width: 260px;
    --presentation-height: 450px;
  }
  .cardBlog .top {
    width: 375px;
    margin: 2rem;
  }
  .cardBlog .round {
    height: 400px;
  }
  .object .grid2 {
    width: var(--grid2-width);
  }
  .object .grid4,
  .object .grid5 {
    width: var(--grid4-width);
  }
  .details img {
    float: right;
    width: var(--grid2-width);
  }
}
@media (min-width: 1024px) {
  :root {
    --grid2-width: 600px;
    --grid4-width: 320px;
  }
}
@media (min-width: 1440px) {
  :root {
    --grid2-width: 750px;
    --grid4-width: 500px;
  }
}
@media (min-width: 2560px) {
  :root {
    --grid2-width: 1200px;
    --grid4-width: 750px;
  }
}/*# sourceMappingURL=app.css.map */